.dailyList{
    display:table;
    width:100%;
}
.daily{
    display: table-row;
    width:100%;
    height:40px;
}

.daily .cell{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    width:33%;
}
.daily img{
    height:25px;
    margin-left:30px;
    margin-right:30px;
}